import {optionify} from '@kevinastone/result';
import * as lunr from 'lunr';

type Result = any;

interface LunrStore {
  [propName: string]: Result;
}

declare global {
  interface Window {
    __LUNR__: {
      __loaded: Promise<{en: {index: lunr.Index; store: LunrStore}}>;
    };
  }
}

export interface SearchResult {
  title: string;
  url: string;
  ref: string;
}

function search(
  index: lunr.Index,
  store: LunrStore,
  query: string,
): SearchResult[] {
  return index
    .query(q => {
      const tokens = lunr.tokenizer(query);

      q.term(tokens, {boost: 100, usePipeline: true});
      q.term(tokens, {
        boost: 10,
        usePipeline: false,
        wildcard: lunr.Query.wildcard.TRAILING,
      });
      q.term(tokens, {boost: 1});
    })
    .map(({ref}) => ({ref, ...store[ref]}));
}

export default async function(query: string): Promise<SearchResult[]> {
  const data = optionify(await window.__LUNR__.__loaded).map(index => index.en);

  return optionify(query)
    .filter(Boolean)
    .andThen(query => data.map(({index, store}) => ({index, store, query})))
    .map(({index, store, query}) => search(index, store, query))
    .unwrapOr([]);
}
