/* eslint @typescript-eslint/generic-type-naming: off */

import {Result as TResult, Ok, Err, resultify} from './result';
import {Option as TOption, Some, None, optionify} from './option';

// Need to do this explicit aliasing because babel struggles with re-exporting
// https://github.com/babel/babel-loader/issues/603
export type Result<T, E> = TResult<T, E>;
export {Ok, Err, resultify};

// Need to do this explicit aliasing because babel struggles with re-exporting
// https://github.com/babel/babel-loader/issues/603
export type Option<T> = TOption<T>;
export {Some, None, optionify};
